$grauity-icons-font: "grauity-icons";

@font-face {
    font-family: $grauity-icons-font;
    src: url("fonts/grauity-icons.eot?11dbc8fa0d3d1838fd4c9b02193e6067#iefix") format("embedded-opentype"),
url("fonts/grauity-icons.woff?11dbc8fa0d3d1838fd4c9b02193e6067") format("woff"),
url("fonts/grauity-icons.woff2?11dbc8fa0d3d1838fd4c9b02193e6067") format("woff2"),
url("fonts/grauity-icons.ttf?11dbc8fa0d3d1838fd4c9b02193e6067") format("truetype");
}

i[class^="grauity-icon-"]:before, i[class*=" grauity-icon-"]:before {
    font-family: grauity-icons !important;
    font-style: normal;
    font-weight: normal !important;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

$grauity-icons-map: (
    "school-left": "\f101",
    "school": "\f102",
    "code": "\f103",
    "today": "\f104",
    "person": "\f105",
);

.grauity-icon-school-left:before {
    content: map-get($grauity-icons-map, "school-left");
}
.grauity-icon-school:before {
    content: map-get($grauity-icons-map, "school");
}
.grauity-icon-code:before {
    content: map-get($grauity-icons-map, "code");
}
.grauity-icon-today:before {
    content: map-get($grauity-icons-map, "today");
}
.grauity-icon-person:before {
    content: map-get($grauity-icons-map, "person");
}
